*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 62.5%;
}

body {
    box-sizing: border-box;
}

ul {
    list-style-type: none;
    margin: 0;
}


a:link,
a:visited,
a:active,
a:hover {
    color: black;
}

a:hover {
    text-decoration: none;
    color: var(--color-primary);
}